import {} from 'antd';
import { Col, Icon, Layout, Menu, Row } from 'antd';
import * as React from 'react';
import { Link } from 'react-router-dom';
const { Header } = Layout;

export interface HeaderProps {
    title: string;
    username?: string;
}
export default class Headers extends React.Component<HeaderProps> {
    public render() {
        const { title, username = 'default' } = this.props;
        return (
            <Header className="header">
                <Row align='middle' justify='start' type='flex' gutter={24}>
                    <Col span={4}>
                        {' '}
                        <div style={{ color: '#fff' }}>{title}</div>
                    </Col>
                    <Col span={4}>
                        {' '}
                        <div style={{ color: '#fff' }}>{username}</div>
                    </Col>
                    <Col span={16 }>
                        <Menu theme={'dark'} mode="horizontal">
                            <Menu.Item key="reactone">
                                <Icon type="mail" />
                                <span>
                                    <Link to="/noiframe/reactone">
                                        <span style={{ color: '#fff' }}>Reactone</span>
                                    </Link>
                                </span>
                            </Menu.Item>
                            <Menu.Item key="reacttwo">
                                <Icon type="mail" />
                                <span>
                                    <Link to="/noiframe/reacttwo">
                                        <span style={{ color: '#fff' }}>Reacttwo</span>
                                    </Link>
                                </span>
                            </Menu.Item>
                            <Menu.Item key="iframeone">
                                <Icon type="appstore" />
                                <span>
                                    <Link to="/haveiframe/iframeone">
                                        <span style={{ color: '#fff' }}>全屏iframe</span>
                                    </Link>
                                </span>
                            </Menu.Item>
                            <Menu.Item key="iframetwo">
                                <Icon type="appstore" />
                                <span>
                                    <Link to="/haveiframe/iframetwo">
                                        <span style={{ color: '#fff' }}>动态加载iframe</span>
                                    </Link>
                                </span>
                            </Menu.Item>
                        </Menu>
                    </Col>
                </Row>
            </Header>
        );
    }
}



