import * as React from 'react';
import { HashRouter as Router, Redirect, Route, Switch } from 'react-router-dom';
import Iframeone from '../iframeone/iframeone.component';
import Iframetwo from '../iframetwo/iframetwo.component';
export default class Haveiframe extends React.Component {
    public state: any = {
        collapsed: false
    };
    public render() {
        return (
            <Router>
                <Switch>
                    <Route exact={true} path="/haveiframe/iframeone" component={Iframeone} />
                    <Route exact={true} path="/haveiframe/iframetwo" component={Iframetwo} />
                    <Redirect path="/haveiframe" to={{ pathname: '/haveiframe/iframeone' }} />
                </Switch>
            </Router>
        );
    }
}



