import { Spin } from 'antd';
import * as React from 'react';
import Iframe from 'react-iframe';
export interface PageState {
    loading: boolean;
}

export default class Iframeone extends React.Component<any, PageState> {
    constructor(props: any) {
        super(props);

        this.state = {
            loading: true
        };
    }

    public render() {
        return (
            <div>
                <Iframe
                    url="http://www.baidu.com"
                    width="100%"
                    height="100%"
                    id="myId"
                    className="myClassname"
                    display="initial"
                    position="fixed"
                    allowFullScreen={true}
                    onLoad={this.toggle}
                />
                {this.state.loading && (
                    <div
                        style={{
                            position: 'fixed',
                            zIndex: 9999,
                            width: '100%',
                            height: '100%',
                            background: 'rgba(0,0,0,0.2)'
                        }}
                    >
                        <div
                            className="flex flex-aic flex-jcc"
                            style={{
                                position: 'relative',
                                width: '100%',
                                height: '100%'
                            }}
                        >
                            <Spin spinning={this.state.loading} />
                        </div>
                    </div>
                )}
            </div>
        );
    }
    public toggle = (value: boolean) => {
        console.log('5s后关闭loading动画');
        setTimeout(() => {
            this.setState({ loading: false });
        }, 5000);
    };
}



