import { Layout } from 'antd';
import * as React from 'react';
import { HashRouter as Router, Redirect, Route, Switch } from 'react-router-dom';
import Headers from '../../common/components/header/header.component';
import Sidebar from '../../common/components/sidebar/sidebar.component';
import Haveiframe from '../haveiframe/haveiframe.component';
import Noiframe from '../noiframe/noiframe.component';
export default class Index extends React.Component {
    public state: any = {
        collapsed: false
    };
    public render() {
        return (
            <Layout style={{ minHeight: '100vh' }}>
                <Sidebar />
                <Router>
                    <Layout>
                        <Headers title="测试React" username="kround" />
                        <Switch>
                            <Route path="/noiframe" component={Noiframe} />
                            <Route path="/haveiframe" component={Haveiframe} />
                            <Redirect path="/" to={{ pathname: '/noiframe' }} />
                        </Switch>
                    </Layout>
                </Router>
            </Layout>
        );
    }
}



