import { Layout } from 'antd';
import * as React from 'react';
import { HashRouter as Router, Redirect, Route, Switch } from 'react-router-dom';
const { Content, Footer } = Layout;
import Reactone from '../reactone/reactone.component';
import Reacttwo from '../reacttwo/reacttwo.component';
export default class Noiframe extends React.Component {
    public state: any = {
        collapsed: false
    };
    public render() {
        return (
            <Layout>
                <Router>
                    <Layout>
                        <Content style={{ margin: '16px' }}>
                            <div style={{ padding: 24, background: '#fff', minHeight: 800 }}>
                                <Switch>
                                    <Route exact={true} path="/noiframe/reactone" component={Reactone} />
                                    <Route exact={true} path="/noiframe/reacttwo" component={Reacttwo} />
                                    <Redirect path="/noiframe" to={{ pathname: '/noiframe/reactone' }} />
                                </Switch>
                            </div>
                        </Content>
                        <Footer style={{ textAlign: 'center' }}>底部</Footer>
                    </Layout>
                </Router>
            </Layout>
        );
    }
}



