import { Alert, Spin, Switch } from 'antd';
import * as React from 'react';
export interface PageState {
    loading: boolean;
    status: string|any;
}

export default class Reacttwo extends React.Component<any, PageState> {
    constructor(props: any) {
        super(props);

        this.state = {
            loading: true,
            status: 'warning'
        };
    }

    public render() {
        return (
            <div>
                <Spin spinning={this.state.loading}>
                    <Alert
                        message="我是React原生页面Reacttwo"
                        description="我现在处于loading状态，可以通过加载按钮来改变状态"
                        type={this.state.status}
                    />
                </Spin>
                <div style={{ marginTop: 16 }}>
                    加载：<Switch checked={this.state.loading} onChange={this.toggle} />
                </div>
            </div>
        );
    }
    public toggle = (value: boolean) => {
        this.setState({ loading: value, status: value ? 'warning' : 'success' });
    };
}



