import { Button, Checkbox, Col, Form, Input, InputNumber, Layout, Row, Spin } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import * as React from 'react';
import Iframe from 'react-iframe';
import './iframetwo.css';
const { Content } = Layout;
class Iframetwoform extends React.Component<FormComponentProps, any> {
    constructor(props: FormComponentProps) {
        super(props);
        this.state = {
            src: '',
            width: 0,
            height: 0,
            loading: false,
            isFull: false
        };
    }
    public render = () => {
        const { getFieldDecorator } = this.props.form;
        return (
            <Layout>
                <Content style={{ margin: '16px' }}>
                    <div style={{ padding: 24, background: '#fff' }}>
                        <Form className="ant-advanced-search-form" onSubmit={this.load}>
                            <Row gutter={24}>
                                <Col span={12} style={{ display: 'block' }}>
                                    <Form.Item label="外部网址">
                                        {getFieldDecorator('src', {
                                            initialValue: this.state.src,
                                            rules: [
                                                {
                                                    required: true,
                                                    message: '请输入外部网址！'
                                                }
                                            ]
                                        })(<Input placeholder="请输入外部网址！" />)}
                                    </Form.Item>
                                </Col>
                                <Col span={4}>
                                    <Form.Item label="ifram宽度">
                                        {getFieldDecorator('width', {
                                            initialValue: this.state.width,
                                            rules: [
                                                {
                                                    required: true,
                                                    message: '请输入ifram宽度！'
                                                }
                                            ]
                                        })(<InputNumber min={0} placeholder="请输入ifram宽度！" />)}
                                    </Form.Item>
                                </Col>
                                <Col span={4}>
                                    <Form.Item label="ifram高度">
                                        {getFieldDecorator('height', {
                                            initialValue: this.state.height,
                                            rules: [
                                                {
                                                    required: true,
                                                    message: '请输入外请输入ifram高度！'
                                                }
                                            ]
                                        })(<InputNumber min={0} placeholder="请输入外请输入ifram高度！" />)}
                                    </Form.Item>
                                </Col>
                                <Col span={4}>
                                    <Form.Item label="自动填充">
                                        {getFieldDecorator('isFull', {
                                            valuePropName: 'checked',
                                            initialValue: this.state.isFull
                                        })(<Checkbox>自动填充</Checkbox>)}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={24} style={{ textAlign: 'right' }}>
                                    <Button onClick={this.clear}>清空</Button>
                                    <Button style={{ marginLeft: 8 }} type="primary" htmlType="submit">
                                        加载
                                    </Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <div
                        style={{
                            padding: 24,
                            marginTop: '16px',
                            height: '800px',
                            background: '#fff',
                            position: 'relative'
                        }}
                    >
                        <Iframe
                            url={this.state.src}
                            width={this.state.isFull ? '100%' : this.state.width}
                            height={this.state.isFull ? '100%' : this.state.height}
                            id="himId"
                            display={this.state.isFull ? 'block' : 'initial'}
                            position="relative"
                            allowFullScreen={this.state.isFull}
                            onLoad={this.close}
                            onError={this.error}
                        />
                        {this.state.loading && (
                            <div
                                style={{
                                    position: 'absolute',
                                    zIndex: 9999,
                                    width: '100%',
                                    height: '100%',
                                    background: 'rgba(0,0,0,0.2)',
                                    top: 0,
                                    left: 0
                                }}
                            >
                                <div
                                    className="flex flex-aic flex-jcc"
                                    style={{
                                        position: 'relative',
                                        width: '100%',
                                        height: '100%'
                                    }}
                                >
                                    <Spin spinning={this.state.loading} />
                                </div>
                            </div>
                        )}
                    </div>
                </Content>
            </Layout>
        );
    };
    public load = (e: any) => {
        e.preventDefault();
        this.props.form.validateFields(
            (err: any, values: { src: string; width: number; height: number; isFull: boolean }) => {
                if (!err) {
                    this.open({
                        src: values.src,
                        width: values.width,
                        height: values.height,
                        isFull: values.isFull,
                        loading: values.src===this.state.src?false:true
                    });
                }
            }
        );
    };

    public clear = () => {
        this.props.form.resetFields();
        this.setState({ loading: false });
    };
    public close = () => {
        this.setState({ loading: false });
    };
    public open = (config: { src: string; width: number; height: number; loading: boolean; isFull: boolean }) => {
        this.setState(config);
    };
    public error = ()=>{
        console.log('error')
    }
}
export default Form.create<FormComponentProps>()(Iframetwoform);



